import axios from "axios";
import moment from "moment";
import { toast } from "react-toastify";
import i18n from "../i18n/config";

export const showToastError = (message) => {
  toast.error(message, {
    position: "top-center",
    autoClose: 3000,
    hideProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
  });
};

export const showToastSuccess = (message) => {
  toast.success(message, {
    position: "top-center",
    autoClose: 3000,
    hideProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
  });
};

export const getOffSet = () => {
  let date = new Date();
  return 0 - date.getTimezoneOffset();
};

export const getEventTime = (eventStartDate, eventEndDate) => {
  let momentStartDate = moment(eventStartDate, "YYYY-MM-DDTHH:mm:SS");
  let momentEndDate = moment(eventEndDate, "YYYY-MM-DDTHH:mm:SS");
  if (momentStartDate.isSameOrBefore(moment(), "day") == true) {
    //Check date is belongs to today or not
    if (
      momentStartDate.isSame(momentEndDate, "day") &&
      momentEndDate.isAfter(moment())
    ) {
      if (momentStartDate.isBefore(moment())) {
        // Event already started
        return (
          i18n.getResource("Now_Until") +
          momentEndDate.local(i18n.language).format("HH:mm")
        );
      } else {
        return (
          i18n.t("Until") +
          momentEndDate.local(i18n.language).format("HH:mm")
        );
      }
    } else if (
      momentStartDate.isBefore(moment()) &&
      momentEndDate.isAfter(moment())
    ) {
      if (momentStartDate.isBefore(moment())) {
        // Event already started and end at diffrent day
        return (
          i18n.t("Now_Until") +
          momentEndDate.local(i18n.language).format("DD-MMM-YYYY HH:mm")
        );
      } else {
        return (
          i18n.t("Until") +
          momentEndDate.local(i18n.language).format("DD-MMM-YYYY HH:mm")
        );
      }
    } else {
      return (
        momentStartDate.local(i18n.language).format("DD-MMM-YYYY HH:mm") +
        " - " +
        momentEndDate.local(i18n.language).format("DD-MMM-YYYY HH:mm")
      );
    }
  } else {
    if (momentStartDate.isSame(momentEndDate, "day") == true) {
      return (
        momentStartDate.local(i18n.language).format("DD-MMM-YYYY HH:mm") +
        " - " +
        momentEndDate.local(i18n.language).format("HH:mm")
      );
    } else {
      return (
        momentStartDate.local(i18n.language).format("DD-MMM-YYYY HH:mm") +
        " - " +
        momentEndDate.local(i18n.language).format("DD-MMM-YYYY HH:mm")
      );
    }
  }
};


