import { useContext } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import AuthContext from "../store/auth-context";
import Dashboard from "../pages/Dashboard";
import LoginPage from "../pages/LoginPage";
import ProfilePage from "../pages/ProfilePage";
import Layout from "../components/Layout/Layout";
import BuyLicensePage from "../pages/BuyLicensePage";
import { useSelector } from "react-redux";
import { STATE } from "../store/state-slice";
import Orders from "../pages/OrdersPage";
import ForgotPassword from "../pages/ForgotPasswordPage";
import SelectPopupPage from "../pages/SelectPopupPage";
import TableMap from "../pages/TableMap";

const RouteList = () => {
  const authCtx = useContext(AuthContext);

  const appState = useSelector((state) => state.appState.AppState);

  return (
    <Layout isLoggedIn={authCtx.isLoggedIn}>
      <Switch>
        <Route path="/auth" exact>
          {!authCtx.isLoggedIn && <LoginPage />}
          {authCtx.isLoggedIn && <Redirect to="/dashboard" />}
        </Route>
        <Route path="/buyingLicense/:userId" exact>
          {!authCtx.isLoggedIn &&
            (appState === STATE.BUYING_LICENSE ? (
              <BuyLicensePage />
            ) : (
              <Redirect to="/auth" />
            ))}
          {authCtx.isLoggedIn && <Redirect to="/dashboard" />}
        </Route>
        <Route path="/" exact>
          {!authCtx.isLoggedIn && <Redirect to="/auth" />}
          {authCtx.isLoggedIn && <Redirect to="/dashboard" />}
        </Route>
        
        <Route path="/forgotPassword" exact>
          {!authCtx.isLoggedIn && <ForgotPassword />}
          {authCtx.isLoggedIn && <Redirect to="/dashboard" />}
        </Route>

        <Route path="/selectPopup" exact>
          {authCtx.isLoggedIn && <SelectPopupPage />}
          {!authCtx.isLoggedIn && <Redirect to="/auth" />}
        </Route>

        <Route path="/tablemap" exact>
          {authCtx.isLoggedIn && <TableMap/>}
          {!authCtx.isLoggedIn && <Redirect to="/auth" />}
        </Route>
       
        <Route path="/dashboard" exact>
          {authCtx.isLoggedIn && <Dashboard />}
          {!authCtx.isLoggedIn && <Redirect to="/auth" />}
        </Route>
       
        <Route path="/orders" exact>
          {authCtx.isLoggedIn && <Orders />}
          {!authCtx.isLoggedIn && <Redirect to="/auth" />}
        </Route>

        <Route path="/profile" exact>
          {authCtx.isLoggedIn && <ProfilePage />}
          {!authCtx.isLoggedIn && <Redirect to="/auth" />}
        </Route>

        <Route path="*">
          {!authCtx.isLoggedIn && <Redirect to="/auth" />}
          {authCtx.isLoggedIn && <Redirect to="/dashboard" />}
        </Route>
      </Switch>
    </Layout>
  );
};

export default RouteList;
