import React, { Fragment, useContext, useEffect, useState } from "react";
import { Card, Col, Table } from "react-bootstrap";
import { Link } from "react-router-dom";
import { FETCH_ALL_CMS_HOSTEDEVENT, POST_METHOD } from "../apis/apiHelper";
import { getEventTime, getOffSet } from "../helper/Helper";
import useHttp from "../hooks/use-http";
import AuthContext from "../store/auth-context";

const SelectPopupPage = () => {
  const { sendRequest: FetchHostedEvents } = useHttp();
  const [allEvents, setAllEvents] = useState([]);

  useEffect(() => {
    FetchHostedEvents(
      {
        url: FETCH_ALL_CMS_HOSTEDEVENT,
        method: POST_METHOD,
        body: {
          offset: getOffSet,
          record_count: 0,
        },
      },
      (response) => {
        if (response.data) {
          setAllEvents(response.data);
        }
        
      }
    );
  }, [FetchHostedEvents]);

  return (
    <Fragment>
      {allEvents.length > 0 && (
        <Col xl={14}>
          <Card>
            <Card.Header>
              <Card.Title>Select Event</Card.Title>
            </Card.Header>
            <Card.Body>
              <Table responsive>
                <thead>
                  <tr>
                    <th></th>
                    <th>
                      <strong>ID</strong>
                    </th>
                    <th>
                      <strong>Title</strong>
                    </th>
                    <th>
                      <strong>Status</strong>
                    </th>
                    <th>
                      <strong>Address</strong>
                    </th>
                    <th>
                      <strong>Date</strong>
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {allEvents.map((event) => {
                    return <EventRow event={event} key={event.id} />;
                  })}
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Col>
      )}
    </Fragment>
  );
};

export default SelectPopupPage;

const EventRow = ({ event }) => {
  return (
    <tr>
      <td>
        <div className="custom-control custom-checkbox checkbox-danger check-sm mr-2">
          <input
            type="checkbox"
            className="custom-control-input"
            id="customCheckBox2"
            required=""
          />
          <label
            className="custom-control-label"
            htmlFor="customCheckBox2"
          ></label>
        </div>
      </td>
      <td>
        {/* For Id */}
        <strong>{event?.id}</strong>
      </td>
      <td>
        <div className="d-flex align-items-center">
          {/* For Title */}
          <span className="w-space-no">{event?.title}</span>
        </div>
      </td>
      {/* For Status */}
      <td>{event?.is_active}</td>

      {/* For Address */}

      <td>
        {/* For Address */}
        {event?.address}
      </td>

      {/* For Date */}
      <td>
        <div className="d-flex align-items-center">
          {/* For Title */}
          <span className="w-space-no">
            {getEventTime(event?.start_date, event?.end_date)}
          </span>
        </div>
        {/* {getEventTime(event?.start_date, event?.end_date)} */}
      </td>
    </tr>
  );
};
