import React, { Fragment , useContext} from "react";
import { Link } from "react-router-dom";
import useInput from "../hooks/use-input";
import useHttp from "../hooks/use-http";
import { useTranslation } from "react-i18next";
import { POST_METHOD, USER_LOGIN } from "../apis/apiHelper";
import AuthContext from '../store/auth-context';
import { useHistory } from 'react-router-dom';
import { showToastError } from "../helper/Helper";

const reg = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;

const isNotEmpty = (value) =>
  value.trim() !== "" && value.length > 6 && value.length < 20;

const isEmail = (value) => reg.test(value);

const ForgotPassword = () => {

  const { t } = useTranslation();
  const history = useHistory();
  const { sendRequest: callLogin } = useHttp();
  const authCtx = useContext(AuthContext);

  const {
    value: emailValue,
    isValid: emailIsValid,
    hasError: emailHasError,
    valueChangeHandler: emailChangeHandler,
    inputBlurHandler: emailBlurHandler,
    reset: resetEmail,
  } = useInput(isEmail);

  const {
    value: passwordValue,
    isValid: passwordIsValid,
    hasError: passwordHasError,
    valueChangeHandler: passwordChangeHandler,
    inputBlurHandler: passwordBlurHandler,
    reset: resetPassword,
  } = useInput(isNotEmpty);

  let formIsValid = false;

  if (emailIsValid && passwordIsValid) {
    formIsValid = true;
  }

  const submitHandler = (event) => {
    event.preventDefault();

    if (!formIsValid) {
      return;
    }

    console.log("Submitted!");
    console.log(emailValue, passwordValue);

    resetEmail();
    resetPassword();

    callApi();
  };

  const emailClasses = emailHasError
    ? "form-control is-invalid"
    : emailIsValid
    ? "form-control is-valid"
    : "form-control";
  const passwordClasses = passwordHasError
    ? "form-control is-invalid"
    : passwordIsValid
    ? "form-control is-valid"
    : "form-control";

  const callApi = () => {
    callLogin(
      {
        url: USER_LOGIN,
        method: POST_METHOD,
        body: {
          login_type: "Normal",
          email: emailValue,
          password: passwordValue,
          device_token: localStorage.getItem("device_token") ?? "",
        },
      },
      (response) => {
        console.log("Response in Login",response);
        if (response.data.resonseCode === 0) {
          showToastError(response.data.responseMessage)
          return
        }
        
        if (response.data.resonseCode === 1) {
          history.push('/buyingLicense');
          return
        }
        const expirationTime = new Date(
          new Date().getTime() + +response.data.token_expiry
        );
        authCtx.login(response.data.token, expirationTime.toISOString(),JSON.stringify(response.data));
        history.replace('/dashboard');
      }
    );
  };

  return (
    <Fragment>
      <div className="row justify-content-center h-100 align-items-center h-80">
        <div className="col-md-6">
          <div className="authincation-content">
            <div className="row no-gutters">
              <div className="col-xl-12">
                <div className="auth-form">
                  <h4 className="text-center mb-4 "> {t("sign_in_note")} </h4>
                  <form
                    action=""
                    onSubmit={(e) => e.preventDefault(submitHandler)}
                  >
                    <div className="form-group">
                      {" "}
                      <label className="mb-1 ">
                        {" "}
                        <strong>{t("Email")}</strong>{" "}
                      </label>
                      <input
                        type="email"
                        className={emailClasses}
                        value={emailValue}
                        onChange={emailChangeHandler}
                        onBlur={emailBlurHandler}
                      />
                    </div>
                    <div className="form-group">
                      <label className="mb-1 ">
                        {" "}
                        <strong>{t("Password")}</strong>{" "}
                      </label>
                      <input
                        type="password"
                        className={passwordClasses}
                        value={passwordValue}
                        onChange={passwordChangeHandler}
                        onBlur={passwordBlurHandler}
                      />
                    </div>
                    <div className="form-row d-flex justify-content-between mt-4 mb-2">
                      <div className="form-group">
                        <Link className="" to="/page-forgot-password">
                          {" "}
                          {t("Forgot_Password")}{" "}
                        </Link>
                      </div>
                    </div>
                    <div className="text-center">
                      <button
                        type="submit"
                        className="btn btn-primary btn-block"
                        onClick={submitHandler}
                      >
                        {" "}
                        {t("Sign_In")}{" "}
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default ForgotPassword;
