const printOrder = async () => {

  //window.open(samplepdf, "PRINT", "height=400,width=600");
  let url = "/eventMenu/getOrderBill/34"
  let method = "GET"
  let body = null
  let headers = { 'Accept': '*/*' }
  let controller = null

  let response = api(url, method, body, headers, controller).then((res) => {

    console.log("API RESPONCE==>", res);

    fetch(res.url)
      .then(response => response.json()).then((res) => {

        printPfd(res.data)

      })


  })

  console.log(response);
}


