import React, { Fragment } from "react";
import useInput from "../hooks/use-input";
import useHttp from "../hooks/use-http";
import { useTranslation } from "react-i18next";
import { POST_METHOD, SAVE_CMS_USERDATA } from "../apis/apiHelper";
import { useHistory, useParams } from "react-router-dom";
import { showToastSuccess } from "../helper/Helper";
import { useDispatch } from "react-redux";
import { STATE, stateAction } from "../store/state-slice";

const reg = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;

const isNotEmpty = (value) => value.trim() !== "";

const isEmail = (value) => reg.test(value);

const BuyLicensePage = () => {
  const { t } = useTranslation();

  const history = useHistory();

  const { sendRequest: saveCMSData } = useHttp();

  const dispatch = useDispatch();

  const { userId } = useParams();

  const {
    value: vatNumber,
    isValid: vatNumberIsValid,
    hasError: vatNumberHasError,
    valueChangeHandler: vatNumberChangeHandler,
    inputBlurHandler: vatNumberBlurHandler,
    reset: resetVatNumber,
  } = useInput(isNotEmpty);

  const {
    value: invoicingAddress,
    isValid: invoicingAddressIsValid,
    hasError: invoicingAddressHasError,
    valueChangeHandler: invoicingAddressChangeHandler,
    inputBlurHandler: invoicingAddressBlurHandler,
    reset: resetInvoicingAddresse,
  } = useInput(isNotEmpty);

  const {
    value: companyName,
    isValid: companyNameIsValid,
    hasError: companyNameHasError,
    valueChangeHandler: companyNameChangeHandler,
    inputBlurHandler: companyNameBlurHandler,
    reset: resetCompanyName,
  } = useInput(isNotEmpty);

  const {
    value: pecEmailValue,
    isValid: pecEmailIsValid,
    hasError: pecEmailHasError,
    valueChangeHandler: pecEmailChangeHandler,
    inputBlurHandler: pecEmailBlurHandler,
    reset: resetPecEmail,
  } = useInput(isEmail);

  let formIsValid = false;

  if (
    companyNameIsValid &&
    invoicingAddressIsValid &&
    pecEmailIsValid &&
    vatNumberIsValid
  ) {
    formIsValid = true;
  }

  const submitHandler = (event) => {
    event.preventDefault();

    if (!formIsValid) {
      return;
    }

    console.log("Submitted!");

    resetCompanyName();
    resetInvoicingAddresse();
    resetPecEmail();
    resetVatNumber();

    callApi();
  };

  const companyClasses = companyNameHasError
    ? "form-control is-invalid"
    : companyNameIsValid
    ? "form-control is-valid"
    : "form-control";

  const invoicClasses = invoicingAddressHasError
    ? "form-control is-invalid"
    : invoicingAddressIsValid
    ? "form-control is-valid"
    : "form-control";

  const pecEmailClasses = pecEmailHasError
    ? "form-control is-invalid"
    : pecEmailIsValid
    ? "form-control is-valid"
    : "form-control";

  const vatClasses = vatNumberHasError
    ? "form-control is-invalid"
    : vatNumberIsValid
    ? "form-control is-valid"
    : "form-control";

  const callApi = () => {
    saveCMSData(
      {
        url: SAVE_CMS_USERDATA,
        method: POST_METHOD,
        body: {
          licenseType: "Free",
          companyName: companyName.trim(),
          invoicingAddress: invoicingAddress.trim(),
          pecEmail: pecEmailValue.trim(),
          vatNumber: vatNumber.trim(),
          userId: userId,
        },
      },
      (response) => {
        console.log("Response in Buying License", response);
        showToastSuccess("Enjoy Popup free services for 2 months.");
        dispatch(stateAction.setState(STATE.STATE_UNDEFINED));
        history.replace("/");
      }
    );
  };

  return (
    <Fragment>
      <div className="row justify-content-center h-100 align-items-center h-80">
        <div className="col-md-6">
          <div className="authincation-content">
            <div className="row no-gutters">
              <div className="col-xl-12">
                <div className="auth-form">
                  <h4 className="text-center mb-4 "> {t("sign_in_note")} </h4>
                  <form
                    action=""
                    onSubmit={(e) => e.preventDefault(submitHandler)}
                  >
                    <div className="form-group">
                      {" "}
                      <label className="mb-1 ">
                        {" "}
                        <strong>{t("company_name")}</strong>{" "}
                      </label>
                      <input
                        type="text"
                        className={companyClasses}
                        value={companyName}
                        onChange={companyNameChangeHandler}
                        onBlur={companyNameBlurHandler}
                      />
                    </div>
                    <div className="form-group">
                      <label className="mb-1 ">
                        {" "}
                        <strong>{t("invoicing_address")}</strong>{" "}
                      </label>
                      <input
                        type="text"
                        className={invoicClasses}
                        value={invoicingAddress}
                        onChange={invoicingAddressChangeHandler}
                        onBlur={invoicingAddressBlurHandler}
                      />
                    </div>

                    <div className="form-group">
                      <label className="mb-1 ">
                        {" "}
                        <strong>{t("pec_email")}</strong>{" "}
                      </label>
                      <input
                        type="email"
                        className={pecEmailClasses}
                        value={pecEmailValue}
                        onChange={pecEmailChangeHandler}
                        onBlur={pecEmailBlurHandler}
                      />
                    </div>

                    <div className="form-group">
                      <label className="mb-1 ">
                        {" "}
                        <strong>{t("vat_number")}</strong>{" "}
                      </label>
                      <input
                        type="text"
                        className={vatClasses}
                        value={vatNumber}
                        onChange={vatNumberChangeHandler}
                        onBlur={vatNumberBlurHandler}
                      />
                    </div>

                    <div className="text-center">
                      <button
                        type="submit"
                        className="btn btn-primary btn-block"
                        onClick={submitHandler}
                      >
                        {" "}
                        {t("start_free_trial")}{" "}
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default BuyLicensePage;
